import Layout from "../../components/Layout"
import { useRouter } from 'next/router'
import posts from '../../static/topic.json'
import postContent from '../../static/postContent.json'
import Link from 'next/link'

const OtherTopics = (props) => {
    const otherTopics = props.posts.filter(post => post.postName != props.currPostName);
    return (
        <div>
            {
                otherTopics.map(topic => (
                    <div className="timelineContainer pt-5">
                        <Link href="/posts/[postName]" as={`/posts/${topic.postName}`}> 
                            <a><img className="img-fluid" src="../../static/assets/template/images/t1-sm.png"/>
                                <p className="textBelow colorWhite fontSize1vw">{topic.name}</p>
                            </a>
                        </Link>
                    </div>
                ))
            }
        </div>
    );
}

class MainTopicArticle extends React.Component
{
    render() {
        const otherArticles = props.currTopicPost.filter(article => article.id != props.currTopicPost[0].id);
        return (
            <div>
                <div>
                    {/* <!-- illustration image --> */}
                    <img className="img-fluid borderRect" src={props.currTopicPost[0].image}/>
                    <a href="#">
                        <h3 className="colorDarkBlue fontRoboBold fontSize1-5vw pt-3">
                            {props.currTopicPost[0].name}
                        </h3>
                    </a>
                    {/* <!-- short excerpt of the article--> */}
                    <p className="fontRoboLight fontSize1-2vw">
                        {props.currTopicPost[0].excerpt}
                    </p>
                </div>
                <hr/>
                {
                    otherArticles.map(article => (
                        <div>
                            <div className="row pt-4">
                                <div className="col-md-3">
                                    <img className="img-fluid" src={article.image}/>
                                </div>
                                <div className="col-md-8">
                                    <a href="#"><h4 className="colorDarkBlue fontSize1-5vw fontRoboBold">{article.name}</h4></a>
                                    <p className="fontSize1-2vw fontRoboLight">{article.excerpt}</p>
                                </div>
                            </div>
                            <hr/>
                        </div>    
                    ))
                }
            </div>
        );
    }
};

const Post = () => {
    const router = useRouter();
    const currPostName = router.query.postName;
    const currName = posts.filter(post => post.postName == currPostName);

    // how do I get the name of this post name?
    return (
        <Layout>
            <h3 className="colorDarkBlue fontSansation fontSize1-5vw pl-5 ml-5">{currName.name}</h3>
            <div className="container-fluid m-4">
                <div className="row">            
                    <div className="col-md-1">
                    </div>
                    <div className="col-md-6 py-2">
                        <MainTopicArticle
                            currTopicPost={postContent
                                .filter(article => article.postName == currPostName)}
                        /> 
                    </div>
                    {/* <!-- for other topics --> */}
                    <div className="col-md-4 vl colScroll">
                        <div className="timelineContainer">
                            <h3 className="fontSize1-5vw">CÁC CHỦ ĐỀ BÀI VIẾT</h3>
                        </div>
                        <OtherTopics posts={posts} currPostName={currPostName}/>
                    </div>
                </div>
            </div>
        </Layout>
    );
}
export default Post;